@charset "utf-8";
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:400,700');

/**
 * Style variables
 */
$base-font-family: 'Source Code Pro', monospace !default;
$base-font-size: 13.5px !default;
$mobile-font-size: 13px !default;
$base-line-height: 1.7 !default;
$container-width: 90% !default;
$container-max-width: 750px !default;

/**
 * Global
 */
body {
  background-color: var(--background-color);
  margin: 0 auto;
  padding: 0;
  font-family: $base-font-family;
  font-size: $base-font-size;
  color: var(--text-color);
  text-align: left;
  line-height: $base-line-height !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: $base-font-size;
  margin: 0px;
  margin-top: 22px;
  font-weight: bold;
  color: var(--text-color);
}
p,
ul,
ol {
  margin: 0px;
  color: var(--text-color);
}
a {
  text-decoration: underline;
  color: var(--link-color);
}
a:hover {
  color: var(--background-color);
  background-color: var(--base-color);
}
.primary-text {
  color: var(--primary-color);
}
@media only screen and (max-device-width: 500px) {
  * {
    font-size: $mobile-font-size !important;
  }
}
